import { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Progress } from '@/components/ui/progress';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { BarChart, Bar, XAxis, YAxis, Tooltip, AreaChart, Area, PieChart, Pie, Cell, ResponsiveContainer, LineChart, Line, ScatterChart, Scatter, ZAxis } from 'recharts';
import { Download } from 'lucide-react';
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';

// Dummy Data
const dummyData = {
  teams: [
    { name: 'Frontend', completedTasks: 75, totalTasks: 100 },
    { name: 'Backend', completedTasks: 60, totalTasks: 80 },
    { name: 'DevOps', completedTasks: 45, totalTasks: 60 },
    { name: 'QA', completedTasks: 80, totalTasks: 90 },
  ],
  tasksOverTime: [
    { date: '2025-08-01', completed: 20, inProgress: 30, pending: 50 },
    { date: '2025-08-15', completed: 40, inProgress: 35, pending: 25 },
    { date: '2025-09-01', completed: 60, inProgress: 20, pending: 20 },
    { date: '2025-09-15', completed: 75, inProgress: 15, pending: 10 },
  ],
  subtasks: [
    { name: 'Completed', value: 120 },
    { name: 'Pending', value: 80 },
  ],
  bugs: [
    { name: 'Open', value: 15 },
    { name: 'Resolved', value: 25 },
    { name: 'Critical', value: 5 },
  ],
  bugTrend: [
    { date: '2025-08-01', bugs: 20 },
    { date: '2025-08-15', bugs: 15 },
    { date: '2025-09-01', bugs: 10 },
    { date: '2025-09-15', bugs: 5 },
  ],
  teamWorkload: [
    { name: 'John', tasks: 25, critical: 5 },
    { name: 'Alice', tasks: 20, critical: 3 },
    { name: 'Bob', tasks: 15, critical: 2 },
    { name: 'Emma', tasks: 18, critical: 1 },
  ],
  meetings: [
    { date: '2025-08-01', topic: 'Sprint Planning', tasks: 10, priority: 3 },
    { date: '2025-08-15', topic: 'Review', tasks: 8, priority: 2 },
    { date: '2025-09-01', topic: 'Retrospective', tasks: 12, priority: 4 },
    { date: '2025-09-15', topic: 'Demo', tasks: 6, priority: 1 },
  ],
  kpi: {
    completion: 75,
    status: 'Good',
  },
  summary: {
    totalTeams: 4,
    totalTasks: 330,
    totalSubtasks: 200,
    bugs: { open: 15, resolved: 25, critical: 5 },
    completedMoms: 4,
  },
};

// Colors for charts
const COLORS = {
  completed: '#22c55e',
  inProgress: '#facc15',
  pending: '#ef4444',
  open: '#3b82f6',
  resolved: '#22c55e',
  critical: '#ef4444',
};

const ProjectReportDashboard = () => {
  const [role, setRole] = useState('Owner');
  const [dateRange, setDateRange] = useState('All');
  const [selectedTeam, setSelectedTeam] = useState('All');

  const handleDownloadPDF = () => {
    const input = document.getElementById('dashboard');
    html2canvas(input).then((canvas) => {
      const imgData = canvas.toDataURL('image/png');
      const pdf = new jsPDF();
      const imgProps = pdf.getImageProperties(imgData);
      const pdfWidth = pdf.internal.pageSize.getWidth();
      const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;
      pdf.addImage(imgData, 'PNG', 0, 0, pdfWidth, pdfHeight);
      pdf.save('project-report.pdf');
    });
  };

  const renderSummaryCards = () => (
    <div className="grid grid-cols-1 md:grid-cols-3 lg:grid-cols-5 gap-4 mb-8">
      <Card>
        <CardHeader>
          <CardTitle>Total Teams</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-2xl font-bold">{dummyData.summary.totalTeams}</p>
        </CardContent>
      </Card>
      <Card>
        <CardHeader>
          <CardTitle>Total Tasks</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-2xl font-bold">{dummyData.summary.totalTasks}</p>
          <Progress value={(dummyData.teams.reduce((sum, t) => sum + t.completedTasks, 0) / dummyData.summary.totalTasks) * 100} className="mt-2" />
        </CardContent>
      </Card>
      <Card>
        <CardHeader>
          <CardTitle>Total Subtasks</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-2xl font-bold">{dummyData.summary.totalSubtasks}</p>
          <Progress value={(dummyData.subtasks.find(s => s.name === 'Completed').value / dummyData.summary.totalSubtasks) * 100} className="mt-2" />
        </CardContent>
      </Card>
      <Card>
        <CardHeader>
          <CardTitle>Bugs</CardTitle>
        </CardHeader>
        <CardContent>
          <p>Open: <Badge variant="destructive">{dummyData.summary.bugs.open}</Badge></p>
          <p>Resolved: <Badge variant="success">{dummyData.summary.bugs.resolved}</Badge></p>
          <p>Critical: <Badge variant="destructive">{dummyData.summary.bugs.critical}</Badge></p>
        </CardContent>
      </Card>
      <Card>
        <CardHeader>
          <CardTitle>Completed MOMs</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-2xl font-bold">{dummyData.summary.completedMoms}</p>
        </CardContent>
      </Card>
    </div>
  );

  const renderTeamProgress = () => (
    <Card className="mb-8">
      <CardHeader>
        <CardTitle>Team Progress</CardTitle>
      </CardHeader>
      <CardContent>
        <ResponsiveContainer width="100%" height={300}>
          <BarChart data={dummyData.teams}>
            <XAxis dataKey="name" />
            <YAxis />
            <Tooltip formatter={(value) => `${value}%`} />
            <Bar dataKey="completedTasks" fill={COLORS.completed} name="Completion %" maxBarSize={50}>
              {dummyData.teams.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={COLORS.completed} />
              ))}
            </Bar>
          </BarChart>
        </ResponsiveContainer>
      </CardContent>
    </Card>
  );

  const renderTasksSubtasks = () => (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-8">
      <Card>
        <CardHeader>
          <CardTitle>Tasks Over Time</CardTitle>
        </CardHeader>
        <CardContent>
          <ResponsiveContainer width="100%" height={300}>
            <AreaChart data={dummyData.tasksOverTime}>
              <XAxis dataKey="date" />
              <YAxis />
              <Tooltip />
              <Area type="monotone" dataKey="completed" stackId="1" fill={COLORS.completed} stroke={COLORS.completed} />
              <Area type="monotone" dataKey="inProgress" stackId="1" fill={COLORS.inProgress} stroke={COLORS.inProgress} />
              <Area type="monotone" dataKey="pending" stackId="1" fill={COLORS.pending} stroke={COLORS.pending} />
            </AreaChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>
      <Card>
        <CardHeader>
          <CardTitle>Subtasks Status</CardTitle>
        </CardHeader>
        <CardContent>
          <ResponsiveContainer width="100%" height={300}>
            <PieChart>
              <Pie data={dummyData.subtasks} dataKey="value" nameKey="name" cx="50%" cy="50%" outerRadius={100} label>
                {dummyData.subtasks.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={entry.name === 'Completed' ? COLORS.completed : COLORS.pending} />
                ))}
              </Pie>
              <Tooltip />
            </PieChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>
    </div>
  );

  const renderBugStatus = () => (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-8">
      <Card>
        <CardHeader>
          <CardTitle>Bug Status</CardTitle>
        </CardHeader>
        <CardContent>
          <ResponsiveContainer width="100%" height={300}>
            <PieChart>
              <Pie data={dummyData.bugs} dataKey="value" nameKey="name" cx="50%" cy="50%" innerRadius={60} outerRadius={100} label>
                {dummyData.bugs.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={COLORS[entry.name.toLowerCase()]} />
                ))}
              </Pie>
              <Tooltip />
            </PieChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>
      <Card>
        <CardHeader>
          <CardTitle>Bug Trend</CardTitle>
        </CardHeader>
        <CardContent>
          <ResponsiveContainer width="100%" height={300}>
            <LineChart data={dummyData.bugTrend}>
              <XAxis dataKey="date" />
              <YAxis />
              <Tooltip />
              <Line type="monotone" dataKey="bugs" stroke={COLORS.critical} />
            </LineChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>
    </div>
  );

  const renderTeamWorkload = () => (
    <Card className="mb-8">
      <CardHeader>
        <CardTitle>Team Workload Distribution</CardTitle>
      </CardHeader>
      <CardContent>
        <ResponsiveContainer width="100%" height={300}>
          <BarChart data={dummyData.teamWorkload}>
            <XAxis dataKey="name" />
            <YAxis />
            <Tooltip />
            <Bar dataKey="tasks" stackId="a" fill={COLORS.completed} />
            <Bar dataKey="critical" stackId="a" fill={COLORS.critical} />
          </BarChart>
        </ResponsiveContainer>
      </CardContent>
    </Card>
  );

  const renderMeetings = () => (
    <Card className="mb-8">
      <CardHeader>
        <CardTitle>MOM Timeline</CardTitle>
      </CardHeader>
      <CardContent>
        <ResponsiveContainer width="100%" height={300}>
          <ScatterChart>
            <XAxis dataKey="date" />
            <YAxis dataKey="priority" />
            <ZAxis dataKey="tasks" range={[50, 400]} name="Tasks" />
            <Tooltip cursor={{ strokeDasharray: '3 3' }} />
            <Scatter name="Meetings" data={dummyData.meetings} fill={COLORS.completed} />
          </ScatterChart>
        </ResponsiveContainer>
      </CardContent>
    </Card>
  );

  const renderProjectHealth = () => (
    <Card className="mb-8">
      <CardHeader>
        <CardTitle>Project Health</CardTitle>
      </CardHeader>
      <CardContent>
        <ResponsiveContainer width="100%" height={300}>
          <RadialBarChart cx="50%" cy="50%" innerRadius="10%" outerRadius="80%" barSize={10} data={[{ value: dummyData.kpi.completion, fill: dummyData.kpi.status === 'Good' ? COLORS.completed : dummyData.kpi.status === 'Warning' ? COLORS.inProgress : COLORS.critical }]}>
            <RadialBar minAngle={15} label={{ position: 'insideStart', fill: '#fff' }} background clockWise dataKey="value" />
            <Tooltip />
          </RadialBarChart>
        </ResponsiveContainer>
      </CardContent>
    </Card>
  );

  const renderOverallReport = () => (
    <Card className="mb-8">
      <CardHeader>
        <CardTitle>Overall Report</CardTitle>
      </CardHeader>
      <CardContent>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div>
            <p>Task Completion</p>
            <ResponsiveContainer width="100%" height={50}>
              <LineChart data={dummyData.tasksOverTime}>
                <Line type="monotone" dataKey="completed" stroke={COLORS.completed} dot={false} />
              </LineChart>
            </ResponsiveContainer>
          </div>
          <div>
            <p>Bug Resolution</p>
            <ResponsiveContainer width="100%" height={50}>
              <LineChart data={dummyData.bugTrend}>
                <Line type="monotone" dataKey="bugs" stroke={COLORS.critical} dot={false} />
              </LineChart>
            </ResponsiveContainer>
          </div>
          <div>
            <p>MOM Adherence</p>
            <ResponsiveContainer width="100%" height={50}>
              <LineChart data={dummyData.meetings}>
                <Line type="monotone" dataKey="tasks" stroke={COLORS.completed} dot={false} />
              </LineChart>
            </ResponsiveContainer>
          </div>
        </div>
      </CardContent>
    </Card>
  );

  return (
    <div id="dashboard" className="p-4 max-w-7xl mx-auto">
      <div className="flex justify-between items-center mb-4">
        <h1 className="text-2xl font-bold">Project Report Dashboard</h1>
        <div className="flex gap-4">
          <Select value={role} onValueChange={setRole}>
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Select Role" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="Owner">Owner</SelectItem>
              <SelectItem value="TL">Team Lead</SelectItem>
              <SelectItem value="Employee">Employee</SelectItem>
            </SelectContent>
          </Select>
          <Select value={dateRange} onValueChange={setDateRange}>
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Select Date Range" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="All">All Time</SelectItem>
              <SelectItem value="Last30">Last 30 Days</SelectItem>
              <SelectItem value="Last7">Last 7 Days</SelectItem>
            </SelectContent>
          </Select>
          <Select value={selectedTeam} onValueChange={setSelectedTeam}>
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Select Team" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="All">All Teams</SelectItem>
              {dummyData.teams.map(team => (
                <SelectItem key={team.name} value={team.name}>{team.name}</SelectItem>
              ))}
            </SelectContent>
          </Select>
          <Button onClick={handleDownloadPDF}>
            <Download className="mr-2 h-4 w-4" /> Download PDF
          </Button>
        </div>
      </div>

      {(role === 'Owner' || role === 'TL') && renderSummaryCards()}
      {(role === 'Owner' || role === 'TL') && renderTeamProgress()}
      {(role === 'Owner' || role === 'Employee') && renderTasksSubtasks()}
      {(role === 'Owner' || role === 'TL') && renderBugStatus()}
      {(role === 'Owner' || role === 'TL') && renderTeamWorkload()}
      {role === 'Owner' && renderMeetings()}
      {role === 'Owner' && renderProjectHealth()}
      {role === 'Owner' && renderOverallReport()}
    </div>
  );
};

export default ProjectReportDashboard;